.liz-click-to-complete-input .popover {
  z-index: 10; }
  .liz-click-to-complete-input .popover input {
    border-radius: 5px; }
.liz-click-to-complete-input .section2 {
  position: relative;
  width: 100%;
  float: left; }
  .liz-click-to-complete-input .section2 .main-img {
    width: 100%;
    float: right; }
  .liz-click-to-complete-input .section2 .item-target {
    position: absolute;
    -webkit-transition: background-color 0.4s ease-out;
    -moz-transition: background-color 0.4s ease-out;
    transition: background-color 0.4s ease-out;
    -webkit-transition: border-radius 0.1s ease-out;
    -moz-transition: border-radius 0.1s ease-out;
    transition: border-radius 0.1s ease-out;
    cursor: pointer; }
    .liz-click-to-complete-input .section2 .item-target:hover {
      border-radius: 50%; }
.liz-click-to-complete-input .right-answer, .liz-click-to-complete-input .wrong-answer {
  position: absolute;
  top: 5%;
  left: 85%;
  width: 25px;
  height: 25px;
  background-size: 25px 25px; }

