.liz-puzzle3 .block-content {
  padding: 5px; }
.liz-puzzle3 .base {
  opacity: 0; }
.liz-puzzle3 .items-container {
  width: calc(50% - 10px);
  float: left;
  position: relative;
  border: 1px solid #000; }
  .liz-puzzle3 .items-container .items .sortable {
    position: absolute;
    border: 2px solid #000;
    /*background: #ededed;*/ }
    .liz-puzzle3 .items-container .items .sortable .item {
      width: 100%;
      height: 100%;
      position: absolute; }
.liz-puzzle3 .targets {
  width: calc(50% - 10px);
  float: right;
  position: relative;
  border: 1px solid #000; }
  .liz-puzzle3 .targets .sortable {
    position: absolute;
    border: 2px solid #000; }
    .liz-puzzle3 .targets .sortable .item {
      width: 100%;
      height: 100%;
      position: absolute; }
.liz-puzzle3 .face {
  margin-left: -72px;
  width: 124px;
  left: 50%;
  z-index: 6;
  top: 20%; }
