.click-transition .item {
  float: left;
  cursor: pointer;
  padding: 5px;
  text-align: center;
}
.click-transition .item p {
  text-align: center;
  font-size: 1.6em;
  letter-spacing: 1px;
}
.click-transition .item img {
  display: inline-block;
  max-height: 256px;
}
.click-transition .img-container {
  position: relative;
  min-height: 150px;
}
.click-transition .img-container img {
  position: absolute;
  margin: auto;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
}
.click-transition .img-container .hide-transition {
  -webkit-animation: fadeOut 0.7s ease-in 0s forwards;
  -moz-animation: fadeOut 0.7s ease-in 0s forwards;
  animation: fadeOut 0.7s ease-in 0s forwards;
}
.click-transition .img-container .back {
  z-index: -1;
}
