.liz-multiple-mark .marks {
  text-align: center;
  margin-bottom: -35px;
}
.liz-multiple-mark .mark {
  width: 70px;
  height: 70px;
  cursor: pointer;
  position: relative;
  z-index: 4;
  display: inline-block;
}
.liz-multiple-mark .item {
  width: 33.33%;
  float: left;
  position: relative;
  text-align: center;
}
.liz-multiple-mark .item img {
  position: relative;
  z-index: 3;
  display: inline-block;
  max-height: 180px;
}
.liz-multiple-mark .target {
  position: absolute;
  bottom: 0;
  right: 0;
  width: 75px;
  height: 75px;
  border: 2px solid #000;
  background: #fff;
}
.liz-multiple-mark .face {
  margin-left: -72px;
  width: 124px;
  left: 50%;
  z-index: 6;
}
